import VueVideoDrawArea from './vue-video-draw-area.vue' // 
import Messages from './message'
const vueVideoDrawArea = {
    install (Vue, options) {
      Vue.prototype.$Messages = Messages;
      if (typeof window !== 'undefined' && window.Vue) {
        window.Vue.use(VueVideoDrawArea);
      } else {
        Vue.component(VueVideoDrawArea.name, VueVideoDrawArea)  // vuePayKeyboard.name name
      }
      //  this.$xxx  
      // Vue.prototype.$xxx  //  this.$xxx 
      // $  
    }
}
export default vueVideoDrawArea // ..


